
import XImg from 'vux/src/components/x-img/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapState} from 'vuex'

export default {
name: 'shopcar',
components: {
XImg,
XButton,
ViewBox,
Divider
},
computed: {
...mapState({
isAndroid: state => state.app.isAndroid,
iosJsUrl: state => state.app.iosJsUrl
})
},
data () {
return {}
},
activated () {
this.share()
},
methods: {
share () {
let link = window.location.href.split('#')[0]
this.wxShare('首页', '信用购机', link, 'https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg')
}
}
}



